const slider = document.querySelector('[data-slider="pic"]');

const initPicSlider = () => {
  if (!slider) {
    return;
  }

  const swiper = new Swiper(slider, {
    slidesPerView: 1, 
    
    spaceBetween: 16,
    loop: true, 
        autoplay: {
          delay: 4000, 
          disableOnInteraction: false, 
        },
        speed: 1500,
  });
};

export { initPicSlider }; 