// export {initScroll};


const initScroll = () => {
    // Проверяем, является ли текущая страница главной
    const isHomePage = window.location.pathname === '/'; // Замените '/' на нужный путь, если у вас другой

    // Проверяем ширину экрана
    const isMobile = window.innerWidth < 1200;

    if (isHomePage && isMobile) {
        // document.querySelector('.main-nav__toggle').addEventListener('click', () => {
        //     document.body.classList.toggle('scroll-lock');
        //     document.querySelector('.header').classList.toggle('is-open');
        // });

        document.querySelectorAll('a[href^="#"], a[href^="/#"]').forEach((link) => {
            link.addEventListener('click', (e) => {
                e.preventDefault();
                const target = link.getAttribute('href').split('#')[1];

                
                if (target) {
                    window.location.hash = target;

                    
                    window.location.reload();
                }
            });
        });
    }
};

window.addEventListener('DOMContentLoaded', () => {
    initScroll();

   
    if (window.location.hash) {
        const targetElement = document.querySelector(window.location.hash);
        if (targetElement) {
            targetElement.scrollIntoView();
        }
    }
});


window.addEventListener('resize', () => {
    initScroll();
});

export{initScroll};